import {useEffect, lazy, Suspense, useState, useContext, useRef} from 'react';
import { Route, Switch, useRouteMatch, useHistory, Redirect } from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
// import {LoadUser} from '../application/middlewares/auth';
// import {getIsLogged} from '../application/selectors/auth';
import { post, fetchData } from '../application/middlewares/index';
// import {Link} from 'react-router-dom'
// import Button from "@material-tailwind/react/Button";
import image from './image/gamysh.jpg';
// import image1 from './image/hotel.jpg'
// import image2 from './image/hotel1.jpg'
// import image3 from './image/hotel2.jpeg'
import { Icon } from 'react-icons-kit'
import {smallDown} from 'react-icons-kit/entypo/smallDown'
// import {smallUp} from 'react-icons-kit/entypo/smallUp'
// import {ic_person} from 'react-icons-kit/md/ic_person'
import {key} from 'react-icons-kit/icomoon/key'
import {office} from 'react-icons-kit/icomoon/office'
import {ic_home_work} from 'react-icons-kit/md/ic_home_work'
import {ic_check} from 'react-icons-kit/md/ic_check'
import { getCategory } from '../application/selectors/auth';
import Lang1 from '../pages/Language/Lang1';
import { LangContext } from "../pages/Language/LangContext";
import useOnClickOutside from '../components/useOnClickOutside'
import useOnClickOutside2 from '../components/useOnClickOutside2'
import useOnClickOutside3 from '../components/useOnClickOutside3'
import useOnClickOutside4 from '../components/useOnClickOutside4'


function Search(){
  const { lang, dispatchLang } = useContext(LangContext);

    const categories = useSelector(getCategory);
    // console.log(categories);

    const dispatch = useDispatch();

    const [locations, setLocations] = useState();
    const [img, setImg] = useState();
    let dil;
    if (lang=='ru') 
    {dil='ru'} 
    else 
    {dil='tm'}

    useEffect(() => {
        dispatch(fetchData({
            url:`${dil}/main-locations`,
            token:'',
            action:(response)=>{
                if(response.success){
                    // console.log("get_location ",response)
                    // console.log(response.data.rows[0].real_estates_all[0])
                    setLocations(response.data.rows);
                    // console.log(products)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    }, [dil]);
    useEffect(() => {
        dispatch(fetchData({
            url:`page-images/1`,
            token:'',
            action:(response)=>{
                if(response.success){
                    console.log("get_location ",response)
                    // console.log(response.data.rows[0].real_estates_all[0])
                    setImg(response.data.rows);
                    console.log(img)
                    // console.log(products)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    }, []);
    useEffect(() => {
        var selects1 = document.querySelectorAll(".labels");
    console.log(selects1[0]);
        console.log(selects1[0]?.children[0]);
        let radio1 = selects1[0]?.children[0].setAttribute('checked', 'checked');
        var selects = document.querySelectorAll(".labels");
        selects.forEach(function(item) {
        if(item.children[0].checked) {
        let q= item.classList.add("labels_active");
        } else {
        let q= item.classList.remove("labels_active");
        }
    });
    }, [categories]);
    
   
    const ToggleClass1 = () => {
        setActive1(!isActive1);
        // setActive2(true);
        // setActive4(true);
        // setActive3(true);
       };
    const ToggleClass2 = () => {
        setActive2(!isActive2);
         
       };
    const ToggleClass3 = () => {
        setActive3(!isActive3);
         
       };
    const ToggleClass4 = () => {
        setActive4(!isActive4);
        
       };
       

    const [isActive1, setActive1] = useState(false);
    const [isActive2, setActive2] = useState(false);
    const [isActive3, setActive3] = useState(false);
    const [isActive4, setActive4] = useState(false);
    const ref = useRef()
    const ref2 = useRef()
    const ref3 = useRef()
    const ref4 = useRef()


    useOnClickOutside(ref, () =>setActive1(false));
    useOnClickOutside2(ref2, () =>setActive2(false));
    useOnClickOutside3(ref3, () =>setActive3(false));
    useOnClickOutside4(ref4, () =>setActive4(false));



    const [category_id, setCategory_id] = useState(1);
    const [category_name, setCategory_name] = useState();
    const [type_id, setType_id] = useState([]);
    const [areaMin, setAreaMin] = useState(null);
    const [areaMax, setAreaMax] = useState(null);
    const [priceMin, setPriceMin] = useState(null);
    const [priceMax, setPriceMax] = useState(null);
    const [location_id, setLocation_id] = useState();
    const [btn1, setBtn1] = useState(true);
    const [btn2, setBtn2] = useState(false);
    const [location_name, setLocation_name] = useState('');
    const [lat, setLat] = useState(37.92523);
    const [lng, setLng] = useState(58.37609)
    const history = useHistory()
    function Values_more(value){
        if(type_id?.includes(value)){
            setType_id(prev => prev.filter(item => item !== value))
        }else{
            setType_id([...type_id, value])
        }
    }
    const SearchFunc = () =>{
        const price = {"min":priceMin, "max":priceMax};
        let priceStr = JSON.stringify(price);
        const area = {"min":areaMin, "max":areaMax};
        let areaStr = JSON.stringify(area);
        console.log(type_id, "------+");
        let stre = ``
        if(selectedRooms.length){
            stre = JSON.stringify([{"id":1, "values":selectedRooms}])
        }
        history.push(`/products?category_id=${category_id ? `${category_id}`: ``}&specs=${stre}&type_id=${type_id ? `${JSON.stringify(type_id)}` : ``}&location_id=${location_id ? `${location_id}` : ``}&price=${priceStr}&area=${areaStr}&main_type_id=${btn1 ? 1 : 2}`)
    }
    const [rooms, setRooms] = useState([]);
    const [roomTypes, setRoomTypes] = useState([])
    useEffect(()=>{
        dispatch(fetchData({
            url:`/room-specification`,
            action: (response) =>{
                console.log(response)
                if(response.success){
                    setRooms(response?.data?.rows[0]?.rooms)
                }
            }
        }))
        // setRoomTypes([])
        dispatch(fetchData({
            url:`/room-filters`,
            action : (response) =>{
                console.log(response)
                if(response.success){
                    response.data?.rows?.map(item => setRoomTypes(prev => [...prev, item.id]))
                }
            }
        }))
    }, [])
    const [selectedRooms, setSelectedRooms] = useState([])
    const [showRooms, setShowRooms] = useState([])
    const [categoriesName, setCategoriesName] = useState([])
    return(
        <main>
            <div className="back_img"
             style={img ? {backgroundImage: `url(https://gamysh.com/${img.destination})` } 
             : 
             {backgroundImage: `none` }}
            >
                <div className="image_gradient">
                <div className="header_text">
                    <h1 className="text-4xl font-medium text-white">#{Lang1[lang].search1}</h1>
                    <div className="select1">
                        {categories?.map((item)=>(
                            <label key={item.id} onClick = {(e)=>{
                                setCategory_id(item.id);
                                var selects = document.querySelectorAll(".labels");
                                selects.forEach(function(item) {
                                    if(item.children[0].checked) {
                                        let q= item.classList.add("labels_active");
                                        } else {
                                            let q= item.classList.remove("labels_active");
                                        }
                                  });
                                // console.log(item.id," ",category_id);
                            }} className="labels flex items-center " >
                            <input type='radio' name="rr"  className="hidden" />
                            <p>{item.name}</p>
                            </label>
                        ))}
                    </div>
                    <div className="select2">
                        <div ref={ref} className="sel_home hover:bg-blue-100 rounded-sm " >
                            <div className="onclick1 px-2 flex justify-between cursor-pointer"   onClick={ToggleClass1}>
                                <div className="flex">
                                    <p>{type_id?.length ? categoriesName?.map((item)=>(item))?.join(', ')?.slice(0,25) : `${Lang1[lang].search2}`}</p>
                                </div>
                                {isActive1 ?  
                                    <Icon size={20} icon={smallDown} className="text-gray-500 mt-2" style={{transform: "rotate(180deg)"}}/>
                                    :
                                <Icon size={20} icon={smallDown} className="text-gray-500" style={{transform: "rotate(0deg)"}} />
                                }
                            </div>
                            <div className={isActive1 ? "dropdown1 shadow-lg border py-2 cursor-pointer"  :  "hidden"}>
                                <div className='flex pl-3 my-2'>
                                <p className={btn1 ? 'btn_drop1 bg-blue-100' :'btn_drop1 hover:bg-blue-100'} onClick={()=> {setBtn1(true); setBtn2(false); setType_id([]); setCategoriesName([]) }}>{Lang1[lang].not11}</p>
                                <p className={btn2 ? 'btn_drop2 bg-blue-100' :'btn_drop2 hover:bg-blue-100'}  onClick={()=> {setBtn1(false); setBtn2(true); setType_id([]); setCategoriesName([])}}>{Lang1[lang].not12}</p>
                                </div>
                            <div className={btn1 ? "drop1" : "hidden"}>
                                        {/* <h3 className="font-medium text-base px-2">first</h3> */}
                                        {categories[category_id-1]?.main_types[0]?.sub_types?.map((item_sub)=>(
                                            <label id={`cc${item_sub.id}`} htmlFor={`c${item_sub.id}`}  key={item_sub.id} onClick = {(e)=>{
                                                Values_more(item_sub.id)
                                                // setCategory_name(item_sub.name);
                                                if(categoriesName?.includes(item_sub.name)){
                                                    setCategoriesName(categoriesName.filter(item => item !== item_sub.name))
                                                }else{
                                                    setCategoriesName(prev => [...prev, item_sub.name])
                                                }
                                                

                                                // console.log(item_sub.id)
                                                // console.log('------')
                                                
                                            }} className="flex items-center text-sm cursor-pointer hover:bg-gray-100">
                                                
                                                <input id={`c${item_sub.id}`}  type="checkbox" name="sub" checked = {type_id?.includes(item_sub.id)} className="pl-8 w-12" />
                                                <p onClick={e => e.stopPropagation()} className='w-full px-2 py-2'>{item_sub.name}</p>
                                            </label> 
                                        ))}
                            </div> 
                            <div className={btn2 ? "drop2" : "hidden"}>
                                        {/* <h3 className="font-medium text-base px-2">second</h3> */}
                                        {categories[category_id-1]?.main_types[1]?.sub_types?.map((item_sub)=>(
                                            <label id={`cc${item_sub.id}`} htmlFor={`c${item_sub.id}`}  key={item_sub.id} onClick = {(e)=>{
                                                Values_more(item_sub.id)
                                                // setCategory_name(item_sub.name);
                                                if(categoriesName?.includes(item_sub.name)){
                                                    setCategoriesName(categoriesName.filter(item => item !== item_sub.name))
                                                }else{
                                                    setCategoriesName(prev =>[...prev, item_sub.name])
                                                }
                                                // console.log(item_sub.id)
                                                // console.log('------')
                                                
                                            }} className="flex items-center text-sm cursor-pointer hover:bg-gray-100">
                                                
                                                <input id={`c${item_sub.id}`}  type="checkbox" name="sub" checked = {type_id?.includes(item_sub.id)} className="pl-8 w-12" />
                                                <p onClick={e => e.stopPropagation()} className='w-full px-2 py-2'>{item_sub.name}</p>
                                            </label> 
                                        ))}
                            </div>

                                
                            </div>
                        </div>
                        
                        {
                            type_id?.every(element =>{
                                return roomTypes?.indexOf(element) !== -1 
                            }) ? 
                            <div ref={ref2} className="sel_qty hover:bg-blue-100 rounded-sm" >
                            <div className="onclick1 px-2 flex justify-between cursor-pointer" onClick={ToggleClass2}>
                                <div className="flex">
                                    {/* <p>{Lang1[lang].search3}</p> */}
                                    <p>{showRooms.length ? `${showRooms.map(item => item.absolute_value).join(", ")} ${Lang1[lang].room}` : Lang1[lang].room_quantity}</p>
                                </div>
                                {isActive2 ?  
                            <Icon size={20} icon={smallDown} className="text-gray-500 mt-2" style={{transform: "rotate(180deg)"}}/>
                            :
                            <Icon size={20} icon={smallDown} className="text-gray-500" style={{transform: "rotate(0deg)"}} />
                                 }
                            </div>
                        <div className={isActive2 ? "dropdown1 shadow-lg border cursor-pointer" :  "hidden"}>
                        <div className="inputs p-4">
                            {rooms?.map(item =>
                                <p className={` ${selectedRooms.includes(item.id) ? `bg-blue-600 text-white` : ``} flex items-center border outline-none 
                                cursor-pointer rounded-md p-2 px-4 mr-2`}
                                onClick = {()=>{
                                    if(selectedRooms.includes(item.id)){
                                        setSelectedRooms(selectedRooms.filter(item1 => item1 != item.id))
                                        setShowRooms(showRooms.filter(item1 => item1.id != item.id))
                                    }else{
                                        setSelectedRooms([...selectedRooms, item.id])
                                        setShowRooms([...showRooms, item])
                                    }
                                }}
                                >{item.absolute_value}</p>
                            )}
                            
                            
                                    {/* <input placeholder={Lang1[lang].dan} className="p-3 focus:outline-none focus:ring-2 focus:ring-blue-600" 
                                    onChange = {(e)=>setAreaMin(e.target.value)}/>
                                    <input placeholder={Lang1[lang].cenli} className="p-3 focus:outline-none focus:ring-2 focus:ring-blue-600" 
                                    onChange = {(e)=>setAreaMax(e.target.value)}/> */}
                        </div>
                            
                        </div>
                            </div>
                        :
                        <>
                        </>
                        }

                        <div ref={ref3} className="sel_price hover:bg-blue-100 rounded-sm">
                        <div className="onclick1 px-2 flex justify-between cursor-pointer" onClick={ToggleClass3}>
                                <div className="flex">
                                    <p className='text-sm'>
                                        {
                                        priceMin || priceMax
                                        ?
                                        <>
                                        {priceMin?.slice(0,9)} - {priceMax?.slice(0,9)}
                                        </> 
                                        :
                                        Lang1[lang].search4
                                        }
                                    </p>
                                </div>
                                 {isActive3 ?  
                            <Icon size={20} icon={smallDown} className="text-gray-500 mt-2" style={{transform: "rotate(180deg)"}}/>
                            :
                            <Icon size={20} icon={smallDown} className="text-gray-500" style={{transform: "rotate(0deg)"}} />
                                 }
                            </div>
                        <div className={isActive3 ? "dropdown1 p-4 shadow-lg border cursor-pointer"  :  "hidden"}>
                        <div className="inputs">
                                    <input placeholder={Lang1[lang].dan} className="p-3 focus:outline-none focus:ring-2 focus:ring-blue-600" 
                                    onChange = {(e)=>setPriceMin(e.target.value)}/>
                                    <input placeholder={Lang1[lang].cenli} className="p-3 focus:outline-none focus:ring-2 focus:ring-blue-600" 
                                    onChange = {(e)=>setPriceMax(e.target.value)}/>
                        </div>
                        </div>
                        </div>

                        <div ref={ref4}className="sel_location hover:bg-blue-100 rounded-sm">
                        <div className="onclick1 px-2 cursor-pointer" onClick={ToggleClass4}>
                            <div className="flex">
                                    <p>{location_name ? location_name : `${Lang1[lang].search5}`}</p>
                            </div>
                        </div>
                            <div className={isActive4 ? "dropdown_city shadow-lg border cursor-pointer"  : "hidden"}>
                            
                                {locations?.map((location)=>(
                                <label  onClick={()=>{
                                    setLocation_id(location?.id);
                                    setLat(location?.lat);
                                    setLng(location?.lng);
                                    
                                    setLocation_name(location?.name);
                                }}
                                className="flex items-center justify-between text-sm cursor-pointer p-2 hover:bg-gray-100">
                                    <p>{location?.name}</p>
                                    {
                                        location_id == location?.id
                                        ?
                                        <>
                                        <input type="radio" name="loc" checked className="mr-3 hidden" />
                                        <Icon size={18} icon={ic_check} className="text-blue-600 mr-2"/>
                                        </>
                                        :
                                        <input type="radio" name="loc" className="mr-3 hidden" />
                                    }
                                {/* <Icon size={18} icon={ic_check} className="text-blue-600"/> */}
                            </label>
                                ))}
                            </div>
                        </div>
                    </div>
                    <div className="flex justify-end mt-4">
                        <button onClick={()=>{
                            history.push(`/AllHomeMap?category_id=${category_id ? `${category_id}`: ``}&type_id=${type_id ? `${JSON.stringify(type_id)}` : ``}&location_id=${location_id ? `${location_id}` : ``}&price=${JSON.stringify({"min":priceMin, "max":priceMax})}&area=${JSON.stringify({"min":areaMin, "max":areaMax})}&lat=${lat}&lng=${lng}`)
                        }} className="btn3 mr-4 font-medium btn100">{Lang1[lang].search6}</button>
                        <button onClick = {()=>SearchFunc()} className="btn1 mr-0 font-medium btn100">{Lang1[lang].search7}</button>
                    </div>
                </div>
                </div>
            </div>
            <div className="main pt-4 bg-gray-100">
                <div className="main1">
                <div className="w-full cards_search">
                <div onClick={()=>{
                    // history.push('/products/1/3');
                    history.push(`/products?category_id=1&type_id=[]&location_id=&price=&area=&main_type_id=1&filter=1`)
                }} className="flex flex-col shadow-lg bg-white rounded-md p-3 h-24 category_mobile cursor-pointer">
                    <Icon size={25} icon={key} className="text-blue-600" />
                    <p className="pt-1">{Lang1[lang].search9}</p>
                </div>
                <div onClick={()=>{
                    // history.push('/products/2/3');
                    history.push(`/products?category_id=2&type_id=[]&location_id=&price=&area=&main_type_id=1&filter=1`)
                }} className="flex flex-col shadow-lg bg-white rounded-md p-3  h-24 category_mobile cursor-pointer">
                    <Icon size={25} icon={ic_home_work} className="text-blue-600" />
                    <p className="pt-1">{Lang1[lang].search8}</p>
                </div>
                <div onClick={()=>{
                    // history.push('/products/3/3');
                    history.push(`/products?category_id=1&type_id=[]&location_id=&price=&area=&main_type_id=2&filter=1`)

                }} className="flex flex-col shadow-lg bg-white rounded-md p-3 h-24 category_mobile cursor-pointer">
                    <Icon size={25} icon={office} className="text-blue-600" />
                    <p className="pt-1">{Lang1[lang].search10}</p>
                </div>
            </div>
                </div>
            </div>
            
        </main>

    )
}

export default Search;